export default {
    onLoad(option){
        this.getwuliuinfo(option)
    },
    data(){
        return {
            doodsinfo:'',
            th_number:'',
            name:'',
            thNumber:'',
            phone:''
        }
    },
    methods:{
        getwuliuinfo(data){
            this.$http.getwuliuinfo(data).then(res=>{
                if(res.status == 200) {
                    this.doodsinfo = res.data.orgoods
                    this.th_number = res.data.th_number
                }
            })
        },
        confirm(){
            if(this.name == '') {
                this.$toast('请填写物流公司名称')
                return false
            }
            if(this.thNumber == '') {
                this.$toast('填写物流单号')
                return false
            }
            if(this.phone.length != 11) {
                this.$toast('填写联系电话')
                return false
            }
            this.$http.getthfahuo({
                th_number:this.th_number,
                ps_name:this.name,
                ps_num:this.thNumber,
                telephone:this.phone
            }).then(res=>{
                if(res.status == 200) {
                    this.$toast(res.mess)
                    setTimeout(()=>{
                        this.getback()
                    },1000)
                }
            })
        }
    }
}
